.collections-page {
  padding-bottom: 50px; }

.collections {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between
    // margin: 0 -15px
    margin: 0 -35px;
    @include md {
      margin: 0 -15px;
      justify-content: center; } }
  &__item {
    @include flex-size(25%);
    padding: 0 15px 50px;
    text-align: center;
    .card {
      display: inline-block; }
    @include md {
      @include flex-size(180px);
      padding: 0 15px 25px; }
    @include xs {
      @include flex-size(100%); } } }
