.card {
  position: relative;
  width: 200px;
  text-align: center;
  @include md {
    width: 150px; }
  @include hover {
    .card__image {
      transform: scale(1.1); } }
  &__image-wrapper {
    display: block;
    position: relative;
    margin-bottom: 20px;
    border-radius: $radius;
    box-shadow: $darkShadow;
    overflow: hidden;
    @include md {
      margin-bottom: 10px; } }
  &__image {
    padding-bottom: 100%;
    background-color: $cardBG;
    @include cover;
    transition: transform .3s ease-in-out;
    transform-origin: center center; }
  &__sale-mark {
    display: inline-block;
    padding: 4px 7px;
    background-color: $purple;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 600;
    color: $white;
    line-height: 1.3;
    position: absolute;
    top: 0;
    left: calc(100% - 11px);
    transform: rotate(90deg);
    transform-origin: 0 0; }
  &__title {
    margin-bottom: 8px;
    @include md {
      margin-bottom: 0; } }
  &__price {
    margin-bottom: 10px;
    color: $text;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.15;
    @include md {
      margin-bottom: 0; }
    * {
      text-decoration: none; } }
  &__like {
    display: block;
    position: absolute;
    bottom: 10px;
    right: 12px;
    font-size: 0;
    cursor: pointer;
    z-index: 5;
    .like {
      background-color: #e1dddd; } }
  &__button {
    position: relative;
    z-index: 5; }
  &__link {
    @include coverdiv;
    z-index: 2; }
  input[type='checkbox'] {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    height: 0;
    &:checked {
      ~ .like {
        background-color: $purple; } } } }

.card-tooltip {
  position: absolute;
  bottom: 4px;
  z-index: 10;
  padding: 35px 20px 12px;
  left: 50%;
  margin-left: -115px;
  width: 230px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
  border: 1px solid #191818;
  border-radius: 5px;
  background-color: $white;
  @include md {
    width: 180px;
    margin-left: -90px; }
  &__close {
    position: absolute;
    top: 13px;
    right: 10px;
    font-size: 0;
    color: #1E194D;
    transition: color .3s;
    background-color: transparent;
    &:before {
      position: absolute;
      content: "";
      top: -8px;
      right: -8px;
      bottom: -8px;
      left: -8px; }
    @include hover {
      color: #f5881f; }
    .icon {
      font-size: 9px; } }
  &__footer {
    margin-top: 15px; }
  &__btn {
    transition: color .3s;
    color: #4D87C6;
    @include hover {
      color: #f5881f; } } }
